<template>
    <div class="search">
        <div class="goback">
            <el-button  icon="el-icon-d-arrow-left" @click="goMain">返回主页</el-button>
        </div>
        <div class="search-content">
            <div class="logo">
                <img src="../assets/images/logo2.png" alt="">
            </div>
            <div class="searching">
                <div class="search-box">
                <el-input  placeholder="搜索新闻资讯/关键字"  v-model="keyword"  @keydown.enter.native="searchEnter">
                     <el-button slot="append" icon="el-icon-search" @click="searchBtn">搜索</el-button>
                </el-input>
            </div>
            </div>
            <div class="advertise">
                <img class="a-imgs" src="http://p2.cri.cn/M00/01/22/rBABCl8-J6SAJphGAAAAAAAAAAA418.1200x100.jpg" alt="">
                <div class="a-text">广告</div>
            </div>
            <div class="contents clearfix">
                <div class="search-list-left-box fl">
                    <div class="list-item clearfix" v-for="(item,index) in SearchData" :key="index" @click="goDetail(item.id)">
                        <div class="left-img fl">
                            <el-image class=" err-imgs">
                                <div slot="error" class="image-slot">
                                    <i class="el-icon-picture-outline"></i>
                                </div>
                            </el-image>
                            <img class="a-img auto-img" :src="item.images" alt="">
                        </div>
                        <div class="right-cont fl">
                            <div class="title">{{item.title}}</div>
                            <div class="text-cont">
                                <span class="post-name">{{item.posterNickName}}</span>
                                <span class="pub-time">日期：{{item.publishTime}}</span>
                                <span class="comment-count">评论：{{item.commentCount}}</span>
                            </div>
                        </div>
                    </div>
                </div>

                <div class="ads-list-right-box fr">
                   <div class="ads-box-one">
                        <el-carousel height="150px">
                            <el-carousel-item v-for="item in adsBOXOneData" :key="item">
                               <img class="imgs auto-img" :src="item" alt="">
                            </el-carousel-item>
                        </el-carousel>
                   </div>

                    <div class="ads-box-two">
                        <div class="ad-box">   
                            <img class="ad-imgs auto-img" src="https://ss2.bdstatic.com/70cFvnSh_Q1YnxGkpoWK1HF6hhy/it/u=3918833539,4216485827&fm=26&gp=0.jpg" alt="">
                            <div class="ad-texts">广告</div>
                        </div>
                        <div class="ad-title">皮卡·Great Wall</div> 
                    </div>
                </div>
            </div>
            
        </div>
    </div>
</template>

<script>
    import "../assets/css/search.less";
    export default {
        data(){
            return{
                keyword:'',
                SearchData:[],
                adsBOXOneData:[
                    'https://i02piccdn.sogoucdn.com/10b64e0355ab4556',
                    'https://i03piccdn.sogoucdn.com/9e114a55766c82a1',
                    'https://i04piccdn.sogoucdn.com/baacd738500b2d38',
                ]

            }
        },
        created(){
           
        },
        methods:{
            getSearchData(aaaa){
                this.axios({
                    method:'GET',
                    url:'/search',
                    params:{
                        apikey: this.appkey,
                        keyword:aaaa,

                    }
                }).then((result) =>{
                    // console.log('result',result);
                    this.SearchData = result.data.data;
                }).catch((err) =>{
                    // console.log('err',err)
                })
            },
            // 按回车键搜索
            searchEnter(e){
                var keyCode = window.event ? e.keyCode : e.which;
                if(keyCode = 13){
                    this.searchBtn();
                }
            },
            searchBtn(){

                 this.getSearchData(this.keyword )
            },
            goDetail(id) {
                // console.log(id);
                this.$router.push({ name: "Detail", query: { id } });
            },
            goMain(){
                this.$router.push({name:'Sole'});
            },

        }
    }
</script>

<style lang="less" scoped>

</style>